.Screen 
{
    width: 60%;
    height: 40%;
    background-color: black;
    border: 10px solid #16b8f3;
    border-radius: 114px;
    box-shadow: 5px 2px 15px rgba($color: grey, $alpha: 0.5), 
     -2px -5px 10px  rgba($color: white, $alpha: 0.5);
    display: flex;
    justify-content: center;
    align-items: center;

     p{
         width: 80%;
         font-size: larger;
     }
}


/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px) {
      .Screen {
          height: 200px;
          width: 200px;
          border-radius: 50%;
 
      }
  }





