
.Wrapper{
    width: 100vw;
    height: 100vh;
    background-color: #16b8f3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    box-sizing: border-box;
}



.UI
{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 80%;
    height: 10%;
}

.Slider
{
    width: 80%;
}

.Tracker
{

    width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    p{
        font-weight: bold;
        font-size: 20px;
    }
}

.SliderContainer
{
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        font-weight: bold;
        font-size: 20px;
    }
}

/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px) {
    .SliderContainer
    {
       width: 90%;
        p{
            font-size: 10px;
        }
    }

    .Tracker
    {
        width: 98%;
        p{
            font-size: 10px;
        }
    }

    .UI
    {
        width: 98%;
    }
  }
